import {
    Button,
    Container,
    Image, 
    Dimmer,
    Segment
} from 'semantic-ui-react'; 
import React from 'react'


 class PublicDomainReader extends React.Component {
     state = {
         activeImage:'', 
         dimmerActive: false
     }
    
   
    handleShow = (e) => {
        console.log(e.target)
        this.setState({ dimmerActive: true })
     }
    handleHide = () => this.setState({ dimmerActive: false })
    
    render(){
        return(
            <div>
                <Image className="/static/pdr-gif-1.gif" onClick={this.handleShow} src={'/static/pdr-gif-1.gif'} size="huge" />
                <Dimmer active={this.state.dimmerActive} onClickOutside={this.handleHide}>
                    <Container>
                        <Image src='/static/pdr-gif-1.gif'  verticalAlign="top"/>
                    </Container>
                </Dimmer>
            </div>
        )
    }
}

export default PublicDomainReader


